import {LangKeyType} from '../../provider/locale/translation/type';
import {Locale} from '../../provider/locale/locale-context';
import {classNames} from '../../util/css';

import uiButtonStyle from './ui-button.scss';

type UiButtonPropsType = {
    className?: string;
    iconSrc?: string;
    isDisabled?: boolean;
    isSquared?: boolean;
    langKey?: LangKeyType;
    onClick?: () => Promise<void> | void;
    textContent?: JSX.Element;
};

export function UiButton(props: UiButtonPropsType): JSX.Element {
    const {onClick, isDisabled, iconSrc, langKey, textContent, isSquared, className} = props;

    return (
        <button
            className={classNames(
                uiButtonStyle.ui_button,
                {
                    [uiButtonStyle.ui_button__disabled]: isDisabled,
                    [uiButtonStyle.ui_button__squared]: isSquared,
                    [uiButtonStyle.ui_button__with_image]: Boolean(iconSrc),
                },
                className
            )}
            onClick={onClick}
            type="button"
        >
            {iconSrc ? <img alt={langKey} className={uiButtonStyle.ui_button__icon} src={iconSrc} /> : null}
            <span className={uiButtonStyle.ui_button__text}>
                {langKey ? <Locale stringKey={langKey} /> : null}
                {textContent || null}
            </span>
        </button>
    );
}
